
































import { ImgVerifyCode, MsgVerifyCode } from '@/components/widgets'
import { Md5 } from 'md5-typescript'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {ImgVerifyCode, MsgVerifyCode},
})
export default class ViewComponent extends Vue {
  /**data */
  // 初始化嵌入页参数
  selectedLabel = ''; // 获取额度页,立即借款页
  vm = this;
  model = {
    mobile: '15222018104',
    imageVerifyCode: '',
    verifyCode: '',
    password: '123qwe',
    // 非必填项
    inviteCode: '', // 邀请码
    openId: '', // 绑定登录相关字段
    // 辅助字段
    uuid: '',
    verifyType: 'register',
  }
  fields = [
    {
      type: "input",
      modelKey: "mobile",
      label: "手机号",
      props: {
        placeholder: '请输入',
        clearable: true,
      },
      rules: {
        required: true,
      },
      // trigger: "blur",// *待优化：在fields中，trigger不生效
      messages: {
        required: "不能为空",
      },
    },
    {
      type: "input",
      modelKey: "imageVerifyCode",
      label: "图片验证码",
      props: {
        placeholder: '请输入',
        clearable: true,
      },
      rules: {
        required: true,
      },
      trigger: "blur",// *待优化：在fields中，trigger不生效
      messages: {
        required: "不能为空",
      },
    },
    {
      type: "input",
      modelKey: "verifyCode",
      label: "短信验证码",
      props: {
        placeholder: '请输入',
        clearable: true,
      },
      rules: {
        required: true,
      },
      trigger: "blur",// *待优化：在fields中，trigger不生效
      messages: {
        required: "不能为空",
      },
    },
    {
      type: "input",
      modelKey: "password",
      label: "密码",
      props: {
        placeholer: "请输入密码",
        type: "password",
        eye: {
          open: false
        },
        clearable: true,
        autocomplete: true,
      },
      rules: {
        required: true
      },
      trigger: "blur",
    },
  ]

  options = {
    scrollToInvalidField: true,
    layout: 'fresh' // standard classic
  }

  /**method */
  async submitHandler(e: any) {
    e.preventDefault();
    const data = await this.$request({
      api: 'register',
      params: {
        mobile: this.model.mobile,
        imageVerifyCode: this.model.imageVerifyCode,
        verifyCode: this.model.verifyCode,
        password: Md5.init(this.model.password),
        // 非必填字段
        inviteCode: this.model.inviteCode,
        openId: this.model.openId,
      },
      method: 'post',
    })
    if(data.resCode === '0000'){
      this.selectedLabel = '注册成功页'
      this.userLosgin()
    }
  }

  async userLosgin(){
    const data = await this.$request({
      api: 'login',
      params: {
        userName: this.model.mobile,
        password: Md5.init(this.model.password),
        // tagCode: 'INVEST',
        // ip: '',
      },
      method:'post',
    })
    if(data.resCode === '0000'){
      const domainApi = this.$store.state.domainApi
      this.$store.commit('resetAll')
      this.$store.commit('setDomainApi', domainApi)
      this.$store.commit('setToken', data.token)
      this.$store.commit('setUser', data.user)

      // this.$router.push({path:'/project/list'})
    }
  }
}
